import React from 'react';
import Actor from './actor';
import Actors from './actors';
import { Route } from 'react-router-dom';
import {ActorsList} from '../data/actors';

const ActorsContainer = (props) => {

    let actorUrl = ActorsList.map((anActor) => {
        return(

            <Route path={`/actors/${anActor.url}`} render={() => <Actor image={anActor.profile_img} name={anActor.name} desc={anActor.description}/>} />
        );
    });

    return (
        <React.Fragment>
            <Route exact path="/actors" render={() => <Actors title="Best Actors"/>} />
            {actorUrl}
        </React.Fragment>

    );

};

export default ActorsContainer;