import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import './css/style.css';
import Navbar from './components/navbar';
import Home from './components/home';
import ActorsContainer from './components/actorsContainer';
import Actress from './components/actress';
import Films from './components/films';


function App() {
  return (

    <BrowserRouter>
      <div className="App">
        <Navbar title="Oscars 2020" />
        <Route exact path="/" component={() => <Home title="Oscar Winners" />} />
        <Route path="/actors/" component={() => <ActorsContainer title="Winning Actors" />} />
        <Route path="/actress/" component={() => <Actress title="Winning Actress" />} />
        <Route path="/films/" component={() => <Films title="Winning Films" />} />
        

      </div>
    </BrowserRouter>
  );
}

export default App;
