import React from 'react';
import {ActorsList} from "../data/actors";
import {Link} from 'react-router-dom';

const Actors = (props) => {

    let theActors = ActorsList.map((aPerson) => {
        return(
            <div className="actor-container">
                <Link to={`/actors/${aPerson.url}`}>
                    <div className="actor-image" style={{ backgroundImage: "url(" + aPerson.img_src + ")" }}></div>
                </Link>
                <h3>{aPerson.name}</h3>
            </div>
        )
    });

    return (
        <div className="main-content">
            <div><Link to="/" className="back">Back</Link></div>
            <h2>{props.title}</h2>
            <div className="container">
                {theActors}
            </div>
        </div>
    );

};

export default Actors;